        {
            type: 'text',
            title: '',
            info: '',
            data: "<p>The list goes on and on, since Maharashtra has an abundant number of top MBBS colleges! As a result, there are more opportunities for students to study MBBS in Maharashtra</p><p>You would receive the highest quality education at these colleges in Maharashtra. You will have more options when it comes to selecting a medical college or institution in Maharashtra that meets your needs and budget! As these colleges have the best infrastructure you will get quality practical lessons too along with theory education.</p><p>In Maharashtra, there is a tremendous growth in various sectors including healthcare, and medicine. Thus, this expands the scope of your MBBS degree.</p><p>After earning your MBBS degree, from one of Maharashtra's best medical schools, you'll have a lot of job opportunities in various private and government hospitals. You can work at laboratories, in colleges as lecturers and as researchers too. You can always start your own private clinic and practice!! </p><p>So, without waiting much, choose your favorite college and pursue a career in medicine and be ready to serve humanity!!</p><p>Best wishes for the future!!</p>"
        }
