        {
            "type": "text",
            "title": "Are State Universities a Good Option for Students?",
            "info": "",
            "data": "<p>State universities are surely a good bet for students. In India, in fact, parents often look forward to bringing their infant into state universities although a deemed or private university with adequate allowance may be just a block away from their residences. The causes for this type of preference are manifold.</p><p>The first and most significant cause is that they are ‘state universities’. This to many is an automatic confirmation of many things. A state university will never be lacking of allowances because the stand is on the state to deliver for their maintenance and supervision. That is a considerable benefit for many: no one likes to take the risk of placing their child in a university where there is no assurance of continuity.Secondly, there is the question of prestige. Some of the oldest organizations of higher studies in India, like the University of Mumbai, the University of Calcutta, and the University of Madras are state universities.</p><p>The third reason is of course understanding. Many families have family members, relatives, or acquaintances that examined and passed out of a state university. That provides the university with an atmosphere of positivity and an automatic dose of goodwill. For many, it is a case of continuity and a necessity for the student to experience the university they went to.</p><p>With this, we reach to an end to our blog on the List of State Universities in India. We hope this blog fed you with all the details you required about the state universities in India.</p>"
        },
        {
            "type": "faq-list",
            "title": "Important FAQs",
            "info": "",
            "data": [
                {
                    "ques": "Q. How many state universities are there in India?",
                    "ans": "As of 22 November 2021, the UGC records 441 state universities"
                },
                {
                    "ques": "Q. Which is the top State University in India?",
                    "ans": "As per the Ranking list of the Best State Universities in India 2022, Jadavpur University, West Bengal comes at the 1st position."
                },
                {
                    "ques": "Q. Are state universities more affordable than private universities?",
                    "ans": "Yes, State universities are comparatively cheaper than private ones - there is no doubt on that."
                },
                {
                    "ques": "Q. What is the key difference between central and state universities in India?",
                    "ans": "Unlike the Central Universities which are financed by the Central Government the State Universities are authorized and managed by the Governments of particular states and they themselves are responsible for organizing, assigning and distributing monetary and other resources."
                }
            ]
        }
