            "data": "<p>Private universities in India are fulfilling the need for quality higher education. Also, they are connecting students to the globe of shiningly bright careers, in India.</p><p>Now think wisely. Your wish to be the best, to make your identity and challenge conventions is possible only at a private university. And private universities represent that excellence. Because what you receive is more than only degrees!</p><p>Choose pragmatically…choose to be future-ready</p><p>All the best!</p>"
