        {
            "type": "text",
            "title": "",
            "info": "",
            "data": '<p>As we have discussed in this blog, there are different types of universities available to students in India. It is very important for students to know about the university they wish to get into.</p><p>This blog will surely help you to know more about the ‘university’ concept and will help you to choose the best university one as per your convenience and wish.</p><p>Choose wisely.</p><p>All the best for your further studies!!</p>'
        },


        {
            "type": "faq-list",
            "title": "Important FAQs",
            "info": "",
            "data": [
                {
                    "ques": "Q. What is the difference between university and college?",
                    "ans": "Universities and colleges mainly vary in course offerings and degree types. University guides to larger institutions delivering both undergraduate and graduate courses. College directs to community academies, technical schools, and liberal arts colleges."
                },
                {
                    "ques": "Q. What are unitary universities?",
                    "ans": "The unitary universities have a single campus and especially focus on postgraduate programs and research, though some may also have requirements for undergraduate studies, such as Banaras Hindu University, Aligarh Muslim University, Jawaharlal Nehru University."
                },
                {
                    "ques": "Q. Which is better - Central University or State University?",
                    "ans": "Both the Central and State universities are good and worthwhile in their own right. Many learners who do not desire to resettle from their hometown choose to go for entry to State University. Also, the rival for admission in State university is comparatively less than in a Central university as the number of candidates in the former is prohibited to the state, while in the case of the latter, applications are accepted from across the nation. Also, State universities have quotes for residence candidates."
                },
                {
                    "ques": "Q. Is Autonomous University private or government?",
                    "ans": "Autonomous universities are financially reinforced by the government but they are liberated to select their programs, curriculum, fees, appointments, and admission procedure."
                },
                {
                    "ques": "Q. Can Private Universities deliver degree programs?",
                    "ans": "Yes, Private universities can offer degree programs, which are identified by the UGC."
                }
            ]
        }
